<template>
  <error-content code="500" :desc="$t_p('message')" :src="src"/>
</template>

<script>
  import error404 from '../../../static/img/error-500.svg'
  import errorContent from './error-content.vue'

  export default {
  name: 'error_500',
  i18n_prefix:'page.error-page.500',
  components: {
    errorContent
  },
  data () {
    return {
      src: error404
    }
  }
}
</script>



